import React from "react";
import under_construction from "../Image/under_construction.png";

function History() {
  return (
    <div class="container">
    <h3 class="text text-decoration-underline text-justify fs-5 text-center fst-italic" style={{fontSize: '3vw' }}>Khmer Ruin</h3>
    <p class="text_P text-justify" >The Khmer ruins stand as a testament to the grandeur and sophistication of the ancient Khmer Empire, which flourished in Southeast Asia from the 9th to the 15th centuries. Among the most renowned structures is the breathtaking Angkor Wat, a colossal temple complex in Cambodia that symbolizes the pinnacle of Khmer architecture. Built in the early 12th century by King Suryavarman II, Angkor Wat is dedicated to the Hindu god Vishnu and later transformed into a Buddhist temple. Its intricate carvings, towering spires, and vast moats evoke a sense of awe, reflecting the Khmer Empire's mastery in engineering and artistry.</p>
    <p class="text_P text-justify mt-3" >Another captivating site is the Bayon Temple, nestled within the ancient city of Angkor Thom. Known for its enigmatic smiling faces carved into massive stone towers, the Bayon is a striking representation of the Khmer's fusion of religion and state. The empire's capital, Angkor Thom, once a bustling metropolis, boasted intricate urban planning, massive reservoirs, and a network of temples, showcasing the Khmer's advanced understanding of city design.</p>
    <p class="text_P text-justify mt-3" >Ta Prohm, enveloped by the embrace of towering silk-cotton trees, exemplifies the harmonious coexistence between nature and architecture. Left partially unrestored, this temple provides a glimpse into the captivating blend of human ingenuity and the relentless force of the jungle. The Khmer ruins, scattered across the Cambodian landscape, reveal not only architectural brilliance but also the rich tapestry of Khmer culture, influenced by Hindu and Buddhist traditions.</p>
    <p class="text_P text-justify mt-3" >While Angkor Wat and its surrounding temples receive international acclaim, numerous lesser-known Khmer ruins dot the Cambodian countryside, each with its own story to tell. These remnants of the Khmer Empire reflect the spiritual, cultural, and technological achievements of a civilization that once dominated the region. As these ruins endure the test of time, they continue to captivate visitors, offering a poignant connection to the glorious past of the Khmer people and their extraordinary legacy.</p>
</div>
  );
}

export default History;
